import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {addToCart,deleteFromCart} from '../store/actions/cartActions'
import Checkout from '../components/Checkout'
export const CartScreen = () => {
    const cartReduderState = useSelector(state => state.addToCartReducer)
    const { cartItems } = cartReduderState;
    const dispatch=useDispatch()
    const subtotal=cartItems.reduce((acc,item)=>acc+(item.price*item.quantity),0)
    return (
        <div>
            <div className="row mt-5 justify-content-center">
                <div className="col-md-8 card text-center">
                    <h1 className='text-center m-5'>MY CART</h1>
                    <table className='table table-bordered'>
                        <thead>
                            <tr>
                                <th>Name</th>
                                <th>Price</th>
                                <th>Quantity</th>
                                <th>Total Price</th>
                                <th>Delete</th>
                            </tr>
                        </thead>
                        <tbody>
                            {cartItems.map((item) => {
                                return <tr key={item._id}>
                                    <td>{item.name}</td>
                                    <td>{item.price}</td>
                                    <td>
                                        <select 
                                        value={item.quantity}
                                        onChange={(e)=>{dispatch(addToCart(item,e.target.value))}}
                                        >
                                            {[...Array(item.countInStock).keys()].map((x,i)=>{
                                                return <option value={i+1}>{i+1}</option>
                                            })}
                                        </select>
                                    </td>
                                    <td>{item.price*item.quantity }</td>
                                    <td><i className='far fa fa-trash-alt'
                                    onClick={()=>dispatch(deleteFromCart(item))}
                                    ></i></td>
                                </tr>
                            })}
                        </tbody>
                    </table>
                    <hr />
                    <h2 className='text-center'>Sub Total:{subtotal} RS/-</h2>
                    <div className='text-center w-100 p-3'>
                    <Checkout amount={subtotal} />
                    </div>
                </div>
            </div>
        </div>
    )
}
