import React, { useState } from 'react';
import {useDispatch}from 'react-redux'
import { filterproducts } from '../store/actions/productActions';
export default function Filter() {
  const [searchKey,setSearchKey]=useState('')
  const [sort,setSort]=useState('popular')
  const [category,setCategory]=useState('all')
  const dispatch=useDispatch()
  const filter=()=>{
    dispatch(filterproducts(searchKey,sort,category))
  }
  return (
    <div className='container my-4'>
      <div className='row justify-content-center align-items-center'>
        <div className="col-md-3 mb-2 mb-md-0">
          <input 
          type="text" 
          placeholder='Search products' 
          value={searchKey} 
          className='form-control' 
          onChange={(e)=>setSearchKey(e.target.value)}
          />
        </div>
        <div className="col-md-2 mb-2 mb-md-0">
          <select name="sort" id="sort" className='form-control'
          value={sort}
          onChange={(e)=>setSort(e.target.value)}
          >
            <option value="popular">Popular</option>
            <option value="htl">High to Low</option>
            <option value="lth">Low to High</option>
          </select>
        </div>
        <div className="col-md-2 mb-2 mb-md-0">
          <select 
          name="category" 
          id="category" 
          value={category}
          onChange={(e)=>setCategory(e.target.value)}
          className='form-control'>
            <option value="all">All</option>
            <option value="electronics">Electronics</option>
            <option value="fashion">Fashion</option>
            <option value="mobiles">Mobiles</option>
            <option value="games">Games</option>
          </select>
        </div>
        <div className="col-md-2">
          <button className='btn btn-primary w-100' onClick={filter}>Search</button>
        </div>
      </div>
    </div>
  );
}
