export const registerNewUserReducer=(state={},action)=>{
    switch (action.type) {
        case 'USER_REGISTER_REQUEST':
            return {
                ...state,
                loading:true
            }
        case 'USER_REGISTER_SUCCESS':
            return {
                ...state,
                loading:false,
                success:true
            }
        case 'USER_REGISTER_FAILURE':
            return {
                ...state,
                loading:false,
                success:false,
                error:true
            }
        default:
            return state
    }
}
export const loginNewUserReducer=(state={},action)=>{
    switch (action.type) {
        case 'USER_LOGIN_REQUEST':
            return {
                ...state,
                loading:true
            }
        case 'USER_LOGIN_SUCCESS':
            return {
                ...state,
                loading:false,
                success:true
            }
        case 'USER_LOGIN_FAILURE':
            debugger
            return {
                ...state,
                loading:false,
                success:false,
                error:true
            }
        case 'USER_LOGOUT':
            return {
                ...state
            }
        default:
            return state
    }
}