import { combineReducers, createStore, applyMiddleware } from 'redux';
import {thunk} from 'redux-thunk'; // Correctly imported thunk
import { getproductbyidreducer, getallproductsreducer } from './reducers/productReducers';
import { addToCartReducer } from './reducers/cardReducers'; // Corrected the import
import { loginNewUserReducer, registerNewUserReducer } from './reducers/userReducers';
import { getOrderbyUseridreducer, getOrderbyidreducer, placeOrderReducer } from './reducers/orderReducers';

const rootReducer = combineReducers({
  getallproductsreducer,
  getproductbyidreducer,
  addToCartReducer,
  registerNewUserReducer,
  loginNewUserReducer,
  placeOrderReducer,
  getOrderbyUseridreducer,
  getOrderbyidreducer
});

const cartItems = localStorage.getItem('cartItems') ? JSON.parse(localStorage.getItem('cartItems')) : [];
const currentUser = localStorage.getItem('currentUser') ? JSON.parse(localStorage.getItem('currentUser')) : null;

const initialState = {
  addToCartReducer: { cartItems },
  loginNewUserReducer:{currentUser}
};

const store = createStore(
  rootReducer,
  initialState,
  applyMiddleware(thunk) // Using applyMiddleware directly
);

export default store;
