export const placeOrderReducer=(state={},action)=>{
    switch (action.type) {
        case 'PLACE_ORDER_REQUEST':
            return {
                ...state,
                loading:true
            }
        case 'PLACE_ORDER_SUCCESS':
            return  {
                ...state,
                loading:false,
                success:true
            }
        case 'PLACE_ORDER_FAILURE':
            return  {
                ...state,
                loading:false,
                error:true
            } 
        default:
            return state
    }
}
export const getOrderbyUseridreducer=(state={},action)=>{
    switch (action.type) {
        case 'GET_ORDERSBYID_REQUEST':
            return {
                ...state,
                loading:true
            }
        case 'GET_ORDERSBYID_SUCCESS':
            return  {
                ...state,
                loading:false,
                success:true,
                order:action.payload
            }
        case 'GET_ORDERSBYID_FAILURE':
            return  {
                ...state,
                loading:false,
                error:true
            } 
        default:
            return state
    }
}

