import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { registerNewUser } from '../store/actions/userActions'
import Loader from '../components/Loader';
import Error from '../components/Error';
import { Success } from '../components/Success';
const RegisterScreen = () => {
    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [cpassword, setcPassword] = useState('')
    const dispatch = useDispatch()
    const registerReducer = useSelector(state => state.registerNewUserReducer)
    const { loading, success, error } = registerReducer
    const register = (e) => {
        e.preventDefault()
        if (password === cpassword) {
            const user = {
                name,
                email,
                password
            }
            dispatch(registerNewUser(user))
        }
        else {
            alert("Passwords are not matched")
        }
    }
    return (
        <div>
            <div className="row justify-content-center">
                <div className="col-md-5 card p-5 m-5">
                    <div className="div">
                        <h1 className='text-center'>Register</h1>
                        {error && <Error error="User already exists" />}
                        {loading && <Loader />}
                        {success && <Success message='Registeted Successfully' />}
                        <form onSubmit={register}>
                            <input
                                type="text"
                                className='form-control'
                                placeholder='name'
                                value={name}
                                onChange={(e) => setName(e.target.value)}
                                required
                            />
                            <input
                                type="email"
                                className='form-control'
                                placeholder='email'
                                value={email}
                                onChange={(e) => setEmail(e.target.value)}
                                required
                            />
                            <input
                                type="password"
                                className='form-control'
                                placeholder='password'
                                value={password}
                                onChange={(e) => setPassword(e.target.value)}
                                required
                            />
                            <input
                                type="password"
                                className='form-control'
                                placeholder='confirm password'
                                value={cpassword}
                                onChange={(e) => setcPassword(e.target.value)}
                                required
                            />
                            <div className="text-center mt-2">
                                <button type='submit'
                                    className='btn btn-primary text-center'
                                >REGISTER</button>
                            </div>
                        </form>
                        <div className="m-3">
                            <a href="/login">
                                Click here to login
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default RegisterScreen