import React from 'react'
import StripeCheckout from 'react-stripe-checkout'
import {useDispatch, useSelector} from 'react-redux'
import {placeOrder} from '../store/actions/orderActions'
import Loader from './Loader'
import {Success} from './Success'
import Error from './Error'
export default function Checkout({amount}) {
  const dispatch=useDispatch()
  const orderState=useSelector(state=>state.placeOrderReducer)
  const {loading,success,error}=orderState;
  const tokenHandler=(token)=>{
      dispatch(placeOrder(token,amount))
  }
  return (
    <div>
      {loading &&<Loader />}
      {error &&<Error />}
      {success &&<Success message="Payment Successfull" />}
      <StripeCheckout
      token={tokenHandler}
      amount={amount}
      shippingAddress
      stripeKey='pk_test_51PYP6pEaFYx8wMOxUgKdBSByeiwNTnP2HaPLIQ5cyNgSLy268uclFBcNAVVtLGzFYiITUWGOOMhy19s5kHIMDmfd00abmwXvfw'
      />
        {/* <button className='btn btn-primary'>PAY NOW</button> */}
    </div>
  )
}
