import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { getorderbyid } from '../store/actions/orderActions';
import { useParams } from 'react-router-dom';
import Loader from '../components/Loader';
import Error from '../components/Error';
export default function OrderInfo() {
    const dispatch = useDispatch();
    const { id } = useParams()
    const orderState = useSelector(state => state.getOrderbyidreducer)
    const { order, loading, error } = orderState
    useEffect(() => {
        dispatch(getorderbyid(id))
    }, [])
    return (
        <div>
            {loading && <Loader />}
            {error && <Error />}
            {
                order && (
                    <div className="row justify-content-center">
                        <div className="col-md-5 card">
                            <h2 className='text-center'>Items in your order</h2>
                            <hr />
                            {
                                order[0].orderItems.map(item => {
                                    return (
                                        <div className="orderitem">
                                            <h1>{item.name}</h1>
                                            <h1>Quantity: {item.quantity}</h1>
                                            <h1>Price:{item.price}</h1>
                                            <hr />
                                        </div>
                                    )
                                })
                            }
                        </div>
                        <div className="col-md-5  text-right">
                            <h2>Order Details</h2>
                            <hr />
                            <h3>OrderId:{order[0]._id}</h3>
                            <h3>Total Amount:{order[0].orderAmount}</h3>
                            <h3>Date:{order[0].createdAt.substring(0, 10)}</h3>
                            <h3>Transaction ID:{order[0].transactionId}</h3>
                            <h3>Transaction ID:{order[0].transactionId}</h3>
                            <h3>{order[0].isDelivered ? 'Order Delivered' : 'Order Placed'}</h3>
                            <hr />
                            <div className="">
                                <h1>Shipping Details</h1>

                                <h1>Address: {order[0].shippingAddress.address}</h1>
                                <h1>City: {order[0].shippingAddress.city}</h1>
                                <h1>Country: {order[0].shippingAddress.country}</h1>
                                <h1>PostalCode: {order[0].shippingAddress.postalCode}</h1>
                            </div>
                        </div>
                    </div>
                )
            }
            <hr />
        </div>
    )
}
