import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getorderbyuserid } from '../store/actions/orderActions'
import Loader from '../components/Loader';
import Error from '../components/Error';
import { Link } from 'react-router-dom';
export default function OrdersScreen() {
    const dispatch = useDispatch()
    const orderState = useSelector(state => state.getOrderbyUseridreducer)
    const { order, error, loading } = orderState
    useEffect(() => {
        if (localStorage.getItem('currentUser')) {
            dispatch(getorderbyuserid())
        }
        else {
            window.location.href = '/login'
        }
    }, [])
    return (
        <div className='container my-5'>
            <div className='row justify-content-center'>
                <div className="col-md-8 text-center mb-4">
                    <h1 className='text-center'>My Orders</h1>
                </div>
            </div>
            <div className='row justify-content-center'>
                <div className="col-md-10">
                    <table className='table table-striped table-hover table-bordered'>
                        <thead className='thead-dark'>
                            <tr>
                                <th>Order ID</th>
                                <th>Amount</th>
                                <th>Date</th>
                                <th>Transaction ID</th>
                                <th>Status</th>
                            </tr>
                        </thead>
                        <tbody>
                            {loading && <Loader />}
                            {error && <Error />}
                            {
                                order && order.map(item => {
                                    return (
                                        <tr onClick={()=>window.location.href=`/orderinfo/${item._id}`}>
                                            <td>{item._id}</td>
                                            <td>{item.orderAmount}</td>
                                            <td>{item.createdAt.substring(0, 10)}</td>
                                            <td>{item.transactionId}</td>
                                            <td>{item.isDelivered ? 'Order Delivered' : 'Order Placed'}</td>
                                        </tr>
                                    )
                                })
                            }
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    );
}
