import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { getproductbyid } from '../store/actions/productActions';
import { addToCart } from '../store/actions/cartActions';
import Loader from '../components/Loader';
import Error from '../components/Error';
export const ProductDescription = () => {
  const { id } = useParams();
  const dispatch = useDispatch();
  const [quantity, setQuantity] = useState(1)
  const getproductbyidstate = useSelector((state) => state.getproductbyidreducer); // Assuming the reducer name is getproductbyidreducer
  const { loading, product, error } = getproductbyidstate;

  useEffect(() => {
    dispatch(getproductbyid(id));
  }, [dispatch, id]);
  const addtocart = () => {
    dispatch(addToCart(product, quantity))
  }
  return (
    <div className="row">
      {loading ? (
        <Loader />
      ) : error ? (
        <Error error='Something went wrong' />
      ) : (
        product && (
          <>
            <div className="col-md-6">
              <div className="card p-2 m-2">
                <h1>{product.name}</h1>
                <img src={product.image} alt="" className="img-fluid m-3 bigimage" />
                <p>{product.description}</p>
              </div>
            </div>
            <div className="col-md-6 text-left">
              <div className="m-2">
                <h1>Price: {product.price}</h1>
                <hr />
                <h1>Select Quantity</h1>
                <select value={quantity} onChange={(e) => setQuantity(e.target.value)}>
                  {[...Array(product.countInStock).keys()].map((x) => (
                    <option key={x + 1} value={x + 1}>
                      {x + 1}
                    </option>
                  ))}
                </select>
                <hr />
                <button className="btn btn-dark" onClick={addtocart}>ADD TO CART</button>
              </div>
            </div>
          </>
        )
      )}
    </div>
  );
};
