import axios from 'axios'
export const getallproducts=()=>dispatch=>{
    dispatch({type:'GET_PRODUCTS_REQUEST'})
    axios.get("/api/product").then(res=>{
      console.log(res.data,"store")
        dispatch({type:'GET_PRODUCTS_SUCCESS',payload:res.data   })
      }).catch(err=>{
        console.log(err)
        dispatch({type:'GET_PRODUCTS_FAILED',payload:err   })
      })
}

export const getproductbyid=(productid)=>dispatch=>{
  dispatch({type:'GET_PRODUCTBYID_REQUEST'})
  axios.post(`/api/product/${productid}`).then(res=>{
      dispatch({type:'GET_PRODUCTBYID_SUCCESS',payload:res.data   })
    }).catch(error=>{
      console.log(error)
      dispatch({type:'GET_PRODUCTBYID_FAILURE',payload:error   })
    })
}

export const filterproducts=(searchkey,sortKey,category)=>dispatch=>{
  dispatch({type:'GET_PRODUCTS_REQUEST'})
  let products=[]
    axios.get("/api/product").then(res=>{
      products=res.data
        if(searchkey){
          products=res.data.filter(item=>item.name.toLowerCase().includes(searchkey))
        }
        if(sortKey!=='popular'){
          if(sortKey==='htl'){
            products=res.data.sort((a,b)=>{
              return -a.price+b.price
            })
          }
          else{
            products=res.data.sort((a,b)=>{
              return a.price-b.price
            })
          }
        }
        if(category!=='all'){
          products=res.data.filter(item=>item.category.toLowerCase().includes(category))
        }
        dispatch({type:'GET_PRODUCTS_SUCCESS',payload:products   })
      }).catch(err=>{
        console.log(err)
        dispatch({type:'GET_PRODUCTS_FAILED',payload:err   })
      })
}