import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { useDispatch, useSelector } from 'react-redux';
import { Navbar as BootstrapNavbar, Nav, NavDropdown, Container } from 'react-bootstrap';
import { logoutuser } from '../store/actions/userActions'

export const Navbar = () => {
    const addToCardReducer = useSelector(state => state.addToCartReducer);
    const { cartItems } = addToCardReducer;
    const currentUser = JSON.parse(localStorage.getItem('currentUser'));
    const dispatch = useDispatch()
    const logout = () => {
        dispatch(logoutuser())
    }
    return (
        <div className='p-2 bg-dark'>
            <BootstrapNavbar bg="dark" variant="dark" expand="lg">
                <Container>
                    <BootstrapNavbar.Brand href="/">Ganesh Shop</BootstrapNavbar.Brand>
                    <BootstrapNavbar.Toggle aria-controls="navbar-nav" />
                    <BootstrapNavbar.Collapse id="navbar-nav" className="justify-content-end">
                        <Nav>
                            {currentUser ? (
                                <NavDropdown title={currentUser.name} id="user-dropdown">
                                    <NavDropdown.Item href="/profile">Profile</NavDropdown.Item>
                                    <NavDropdown.Item href="/orders">Orders</NavDropdown.Item>
                                    <NavDropdown.Item onClick={logout}>Logout</NavDropdown.Item>
                                </NavDropdown>
                            ) : (
                                <Nav.Link href="/login">Login</Nav.Link>
                            )}
                            <Nav.Link href="/cart">
                                <i className='fas fa-shopping-cart'>
                                    {cartItems.length}
                                </i>
                            </Nav.Link>
                        </Nav>
                    </BootstrapNavbar.Collapse>
                </Container>
            </BootstrapNavbar>
        </div>
    );
};
