import axios from "axios"
 
export const registerNewUser=(user)=>dispatch=>{
    dispatch({type:'USER_REGISTER_REQUEST'})
    axios.post('/api/user/register',user).then((res)=>{
        dispatch({type:'USER_REGISTER_SUCCESS'})
    })
    .catch(err=>{
        dispatch({type:'USER_REGISTER_FAILURE ',err})
        console.log(err)
    })
}
export const loginNewUser=(user)=>dispatch=>{
    dispatch({type:'USER_LOGIN_REQUEST'})
    axios.post('/api/user/login',user).then((res)=>{
        debugger
        dispatch({type:'USER_LOGIN_SUCCESS'})
        localStorage.setItem('currentUser',JSON.stringify(res.data))
        window.location.href='/'
    })
    .catch(err=>{
        debugger
        dispatch({type:'USER_LOGIN_FAILURE ',payload: err.message })
    })
}
export const logoutuser=()=>dispatch=>{
    localStorage.removeItem('currentUser')
    localStorage.removeItem('cartItems')
    dispatch({type:'USER_LOGOUT'})
    window.location.href='/login'

}