import axios from 'axios'
export const placeOrder=(token,subtotal)=>(disptach,getState)=>{
    const currentUser=getState().loginNewUserReducer.currentUser;
    const demoItems=getState().addToCartReducer.cartItems
    const cartItems=[]
    demoItems.forEach(element => {
        const item={
            name:element.name,
            quantity:element.quantity,
            price: element.price,
            _id:element._id
        }
        cartItems.push(item)
    });
    disptach({type:'PLACE_ORDER_REQUEST'})
    axios.post('/api/order',{token,subtotal,currentUser,cartItems})
    .then((res) => {
        disptach({type:'PLACE_ORDER_SUCCESS'})
    })
    .catch(err=>{
        disptach({type:'PLACE_ORDER_FAILURE'})
    })
}

export const getorderbyuserid=()=>(disptach,getState)=>{
    disptach({
        type:'GET_ORDERSBYID_REQUEST'
    })
    const userid=getState().loginNewUserReducer.currentUser._id
    axios.get(`/api/order/${userid}`)
    .then((res) => {
        disptach({type:'GET_ORDERSBYID_SUCCESS',payload:res.data})
    })
    .catch(err=>{
        disptach({type:'GET_ORDERSBYID_FAILURE',payload:err})
    })
}
export const getorderbyid=(orderid)=>(disptach,getState)=>{
    disptach({
        type:'GET_ORDERBYID_REQUEST'
    })
    axios.get(`/api/order/getsingleorder/${orderid}`)
    .then((res) => {
        disptach({type:'GET_ORDERBYID_SUCCESS',payload:res.data})
    })
    .catch(err=>{
        disptach({type:'GET_ORDERBYID_FAILURE',payload:err})
    })
}